.Container {
	max-width: 1080px;
	margin: 0 auto;
	padding: 0 16px;
}
.Header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	gap: 12px;
	padding: 8px 0;
	margin-bottom: 16px;
	border-bottom: 1px solid rgb(161, 91, 0);
}
.Header > nav {
	display: flex;
}
.Link   {
	padding: 8px 16px;
	border-radius: 4px;
	text-decoration: none;
	color: black;
	font-weight: 500;
	
}
.Link:global(.active) {
	font-weight: 800;
	color: orange;
	text-decoration: underline;
	
}

